import { styled } from '@mui/material/styles';

export const StyledComment = styled('div')(() => ({
  display: 'flex',
  flexDirection: 'column',
  padding: '24px 16px',
  background: '#fff',
  borderRadius: '16px',
  transition: '0.3s',
  position: 'relative',
  boxShadow: '0px 147px 59px rgba(0, 0, 0, 0.01), 0px 83px 50px rgba(0, 0, 0, 0.03), 0px 37px 37px rgba(0, 0, 0, 0.04), 0px 9px 20px rgba(0, 0, 0, 0.05), 0px 0px 0px rgba(0, 0, 0, 0.05)',
  ['&:hover']: {
    transform: 'scale(1.08)',
  }
}));

export const StyledHead = styled('div')(() => ({
  display: 'flex',
  gap: '12px',
  marginBottom: '18px',
  height: '48px'
}));

export const StyledPhoto = styled('img')(() => ({
  /* width: '48px', */
  height: '100%',
  borderRadius: '50%',
}));

export const StyledNameAndStars = styled('div')(() => ({
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'space-between',
/*   height: '100%', */
  paddingTop: '4px',
  /* paddingBottom: '4px' */
}));

export const StyledName = styled('div')(() => ({
  fontSize: '18px',
  lineHeight: '140%'
}));

export const StyledStars = styled('div')(() => ({
  display: 'flex',
  gap: '2px',
  height: '14px',
}));

export const StyledStar = styled('img')(() => ({
  height: '100%',
}));

export const StyledTitle = styled('p')(() => ({
  padding: '0',
  margin: '0 0 8px',
  fontSize: '18px',
}));

export const StyledExtra = styled('div')(() => ({
  fontSize: '14px',
  cursor: 'pointer',
  overflow: 'hidden',
  display: '-webkit-box',
  WebkitLineClamp: '3',
  WebkitBoxOrient: 'vertical',
  /* textOverflow: 'ellipsis',
  transition: '0.4s linear-gradient',
  ['&:hover']: {
    maxHeight: 'fit-content',
  } */
}))