import { Button } from '@mui/material';
import { styled } from '@mui/material/styles';

export const StyledIronman = styled('div')(() => ({
  display: 'flex',
  gap: '40px',
  padding: '0 40px',
  overflow: 'hidden',
  position: 'relative',
  boxShadow: '0px 147px 59px rgba(0, 0, 0, 0.01), 0px 83px 50px rgba(0, 0, 0, 0.03), 0px 37px 37px rgba(0, 0, 0, 0.04), 0px 9px 20px rgba(0, 0, 0, 0.05), 0px 0px 0px rgba(0, 0, 0, 0.05)',
}));

export const StyledImage = styled('img')(() => ({
  flex: '0.5',
  width: '50%',
}));

export const StyledContent = styled('div')(() => ({
  flex: '0.5',
  width: '50%',
  padding: '70px 0',
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'center',
}));

export const StyledLink = styled('a')(() => ({
  color: '#000',
  fontSize: '16px',
  letterSpacing: '2px',
  textDecoration: 'none',
  marginBottom: '10px',
  textTransform: 'uppercase'
}));

export const StyledSlogan = styled('div')(() => ({
  fontSize: '38px',
  fontWeight: '800',
  lineHeight: '140%',
  width: '70%',
}));

export const StyledTitle = styled('h1')(() => ({
  padding: '0',
  margin: '0',
  marginBottom: '40px',
  fontSize: '100px',
  fontWeight: '800',
  lineHeight: '140%',
}));

export const StyedAbout = styled('div')(() => ({
  marginBottom: '60px',
  fontSize: '16px',
  lineHeight: '150%',
  maxWidth: '90%'
}));

export const StyledButton = styled(Button)(() => ({
  padding: '16px 40px',
  color: '#fff',
  borderRadius: '12px',
  background: 'linear-gradient(90deg, #396AFC 0%, #2948FF 100%)',
  width: '50%',
  outline: '1px solid #396AFC',
  transition: '0.3s',
  ['&:hover']: {
    color: '#396AFC',
    background: '#fff'
  }
}));

export const StyledDots = styled('div')(() => ({
  display: 'flex',
  gap: '10px',
  position: 'absolute',
  right: '40px',
  top: '70px'
}));

export const StyledDot = styled('div')(() => ({
  width: '20px',
  height: '20px',
  borderRadius: '50%',
  background: '#FFB800'
}));

export const StyledSocialBlock = styled('div')(() => ({
  display: 'flex',
  alignItems: 'center',
  position: 'absolute',
  bottom: '70px',
  left: '40px',
  gap: '18px',
  flexWrap: 'wrap',
}));

export const StyledSocial = styled('a')(() => ({
  width: '30px',
  height: '30px',
  overflow: 'hidden',
  padding: '0'
}));

export const StyledSocialIcon = styled('img')(() => ({
  width: '100%',
  height: '100%'
}));