import {
  StyledComment,
  StyledExtra,
  StyledHead,
  StyledName,
  StyledNameAndStars,
  StyledPhoto,
  StyledStar,
  StyledStars,
  StyledTitle,
} from './styled';

import star from '../../../../images/grade.png';

interface Props {
  comment: CommentOne,
  user: User
}

export const Comment: React.FC<Props> = ({ comment, user }) => {
  const fillStars = () => {
    let stars = [];
    for (let i = 1; i <= comment.stars; i++) {
      stars.push(i);
    }

    return stars;
  };

  const stars = fillStars();

  console.log(stars);

  return (
    <StyledComment>
      <StyledHead>
        <StyledPhoto
          src={user.photo}
          alt="Author Photo"
        />
        <StyledNameAndStars>
          <StyledName>{user.name}</StyledName>
          <StyledStars>
            {stars.map((s) => (
              <StyledStar src={star} />
            ))}
          </StyledStars>
        </StyledNameAndStars>
      </StyledHead>
      <StyledTitle>
        {comment.title}
      </StyledTitle>
      <StyledExtra>
        {comment.extra}
      </StyledExtra>
    </StyledComment>
  )
}