import { styled } from '@mui/material/styles';

export const StyledComments = styled('section')(() => ({
  gridColumn: '1/13',
  marginBottom: '280px',
  position: 'relative',
  zIndex: '1',
}));

export const StyledCommentsContainer = styled('div')(() => ({
  maxWidth: '1440px',
  padding: '0 20px',
  margin: 'auto',
  display: 'grid',
  gridTemplateColumns: 'repeat(4, 1fr)',
  gap: '45px',
  ['@media (max-width: 980px)']: {
    gap: '30px',
  }
}))

export const StyledPot = styled('img')(() => ({
  position: 'absolute',
  top: '-20%',
  width: '40%',
  height: '130%',
}))

export const StyledCommentsColumn = styled('div')(() => ({
  display: 'flex',
  flexDirection: 'column',
  gap: '22px',
  ['@media (max-width: 980px)']: {
    flexDirection: 'row',
  }
}))