import { styled } from '@mui/material/styles';

export const StyledFooter = styled('footer')(() => ({
  gridColumn: '1/13',
  display: 'grid',
  gridTemplateColumns: 'repeat(12, 1fr)',
  padding: '76px 0 40px',
  gap: '90px 30px'
}));

export const StyledLogo = styled('a')(() => ({
  gridColumn: '1/3',
  cursor: 'pointer',
  padding: '0',
  aspectRatio: '9/2.2'
}));

export const StyledImage = styled('img')(() => ({
  width: '100%',
  height: '100%'
}));

export const StyledLinksBlock = styled('div')(() => ({
  display: 'flex',
  flexDirection: 'column',
  gap: '30px',
  /* alignItems: 'center',
  justifyContent: 'center' */
}));

export const StyledLinksTitle = styled('div')(() => ({
  fontSize: '24px',
  fontWeight: '600',
  lineHeight: '200%'
}));

export const StyledLinks = styled('div')(() => ({
  display: 'flex',
  flexDirection: 'column',
  gap: '18px',
}));

export const StyledLink = styled('a')(() => ({
  fontSize: '14px',
  color: '#000',
  textDecorationLine: 'none',
  cursor: 'pointer',
  fontWeight: '500',
  transition: '0.3s',
  ['&:hover']: {
    /* transform: 'scale(1.02)', */
    fontWeight: '700'
  }
}));

export const StyledSocialBlock = styled('div')(() => ({
  display: 'flex',
  alignItems: 'center',
  gap: '18px',
  flexWrap: 'wrap',
}));

export const StyledSocial = styled('a')(() => ({
  width: '30px',
  height: '30px',
  overflow: 'hidden',
  padding: '0'
}));

export const StyledSocialIcon = styled('img')(() => ({
  width: '100%',
  height: '100%'
}));

export const StyledBottom = styled('div')(() => ({
  gridColumn: '1/13',
  display: 'flex',
  justifyContent: 'space-between',
}));

export const StyledCopyRight = styled('div')(() => ({
  fontSize: '14px',
  lineHeight: '140%',
}))

export const StyledPrivacy = styled('div')(() => ({
  fontSize: '14px',
  lineHeight: '140%',
}))