import {
  StyledBenefit,
  StyledBenefits,
  StyledButton,
  StyledContent,
  StyledIcon,
  StyledPack,
  StyledPrice,
  StyledPriceText,
  StyledTitle
} from './styled';

import CheckCircleRoundedIcon from '@mui/icons-material/CheckCircleRounded';
import CancelRoundedIcon from '@mui/icons-material/CancelRounded';

interface Props {
  pack: any
};

export const Pack: React.FC<Props> = ({ pack }) => {
  let grid = '';

  if (pack.title === 'Sprinter') {
    grid = '1/2'
  } else if (pack.title === 'Marathon') {
    grid = '2/3'
  } else if (pack.title === 'Mentoring') {
    grid = '3/4'
  };

  return (
    <StyledPack grid={grid}>
      <StyledPrice back={pack.back}>
        <StyledPriceText>${pack.price}/course</StyledPriceText>
      </StyledPrice>
      <StyledContent>
        <StyledTitle color={pack.color}>
          <StyledIcon src={pack.icon} />
          {pack.title}
        </StyledTitle>
        <StyledBenefits>
          {[...pack.benefits].map((b) => (
            <StyledBenefit key={b.id}>
              {b.value ? (
                <CheckCircleRoundedIcon sx={{color: pack.color}} />
              ) : (
                <CancelRoundedIcon sx={{color: '#FFB800'}} />
              )}
              {b.title}
            </StyledBenefit>
          ))}
        </StyledBenefits>
        <StyledButton back={pack.back}>Shop Now</StyledButton>
      </StyledContent>
    </StyledPack>
  )
}