import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';

export const StyledItem = styled('div')(() => ({
  display: 'flex',
  flexDirection: 'column',
  gap: '30px',
  padding: '24px 48px',
  borderRadius: '12px',
  border: '2px solid #62B2FF',
  cursor: 'pointer'
}));

export const StyledTop = styled('div')(() => ({
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
  width: '100%'
}));

interface Props {
  show: boolean
}

export const StyledQuestion = styled('div')<Props>(({ show }) => ({
  fontSize: '24px',
  fontWeight: show ? '800' : '600',
  lineHeight: '120%',
  color: show ? '#0083FF' : '#000'
}));

export const StyledButton = styled(Button)(() => ({
  minWidth: '0',
  width: '12px',
  height: '12px',
  color: '#000'
}));

export const StyledAnswer = styled('div')(() => ({
  fontSize: '18px',
  lineHeight: '140%'
}))