import { Button } from '@mui/material';
import { styled } from '@mui/material/styles';

export const StyledJoin = styled('section')(() => ({
  gridColumn: '1/13',
  position: 'relative',
  marginBottom: '140px',
  paddingTop: '10%'
}));

export const StyledContent = styled('div')(() => ({
  padding: '50px 64px',
  background: 'linear-gradient(90deg, #2948FF 0%, #396AFC 100%)',
  borderRadius: '12px',
  display: 'flex',
  paddingBottom: '70px'
}))

export const StyledTextBlock = styled('div')(() => ({
  display: 'flex',
  flexDirection: 'column',
  maxWidth: '64%',
  color: '#fff',
}));

export const StyledTitle = styled('div')(() => ({
  fontSize: '70px',
  lineHeight: '140%',
  fontWeight: '600',
  marginBottom: '10px'
}));

export const StyledExtra = styled('div')(() => ({
  fontSize: '34px',
  lineHeight: '120%',
  marginBottom: '80px'
}));

export const StyledPhoto = styled('img')(() => ({
  position: 'absolute',
  top: '0',
  /* height: '100%', */
  right: '20px',
  bottom: '0'
}));

export const StyledForm = styled('form')(() => ({
  display: 'flex',
  gap: '20px'
}));

export const StyledInput = styled('input')(() => ({
  padding: '10px 12px',
  flex: '0.7',
  fontSize: '18px',
  lineHeight: '27px',
  color: '#000',
  border: 'none',
  borderRadius: '4px',
  ['&:placeholder']: {
    color: '#002033'
  },
  ['&:focus']: {
    border: 'none',
  }
}));

export const StyledButton = styled(Button)(() => ({
  minWidth: '0',
  flex: '0.3',
  height: '100%',
  background: '#62B2FF',
  color: '#fff',
  fontSize: '18px'
}))