import {
  StyledContact,
  StyledDivider,
  StyledHeader,
  StyledHeaderContainer,
  StyledJoin,
  StyledLink,
  StyledLinks,
  StyledLogo,
} from './styled';
import logo from '../../../../images/Logo.png';
import ArrowForwardOutlinedIcon from '@mui/icons-material/ArrowForwardOutlined';
import { useEffect, useState } from 'react';

export const Header = () => {
  const [fixed, setFixed] = useState(false);

  useEffect(() => {
    window.addEventListener("scroll", listenToScroll);
    return () =>
       window.removeEventListener("scroll", listenToScroll);
  }, []);

  const listenToScroll = () => {
    let heightToHideFrom = 900;
    const winScroll = document.body.scrollTop ||
        document.documentElement.scrollTop;
  
    if (winScroll > heightToHideFrom) {
      setFixed(true);
    } else {
      setFixed(false);
    }
  };

  console.log(fixed)

  return (
    <StyledHeader fixed={fixed} >
      <StyledHeaderContainer>
        <StyledLogo
          src={logo}
          alt='Maple Leaf Tech Academy Logo'
        />
        <StyledLinks>
          <StyledLink href='#' target="_self" >Home</StyledLink>
          <StyledLink href='#' target="_self">Advantages</StyledLink>
          <StyledLink href='#' target="_self">Courses</StyledLink>
          <StyledLink href='#' target="_self">About us</StyledLink>
          <StyledLink href='#' target="_self">Feedback</StyledLink>
          <StyledLink href='#' target="_self">Contacts</StyledLink>
        </StyledLinks>
        <StyledContact>
          <StyledLink href='mailto: info@mapleleaf.com' sx={{
            /* paddingRight: '18px',
            borderRight: '1px solid #000', */
            marginRight: '18px'
          }} >
            info@mapleleaf.com
          </StyledLink>
          <StyledDivider sx={{marginRight: '18px'}} />
          <StyledJoin>
            Join for free
          </StyledJoin>
        </StyledContact>
      </StyledHeaderContainer>
    </StyledHeader>
  )
}