import {
  StyledAnswer,
  StyledButton,
  StyledItem, StyledQuestion, StyledTop
} from './styled';

import AddRoundedIcon from '@mui/icons-material/AddRounded';
import RemoveRoundedIcon from '@mui/icons-material/RemoveRounded';
import { useState } from 'react';

interface Props {
  item: any
}

export const Item: React.FC<Props> = ({ item }) => {
  const [show, setShow] = useState(false);

  const handleShow = () => {
    setShow(!show)
  };

  return (
    <StyledItem onClick={() => handleShow()}>
      <StyledTop>
        <StyledQuestion show={show}>{item.question}</StyledQuestion>
        <StyledButton>
          {!show ? (
            <AddRoundedIcon />
          ) : (
            <RemoveRoundedIcon />
          )}
        </StyledButton>
      </StyledTop>
      {
        show && (
          <StyledAnswer>{item.answer}</StyledAnswer>
        )
      }
    </StyledItem>
  )
}