import { styled } from '@mui/material/styles';

export const StyledFeedbacks = styled('section')(() => ({
  gridColumn: '1/13',
  marginBottom: '140px',
  display: 'flex',
  flexDirection: 'column'
}));

export const StyledTitle = styled('h3')(() => ({
  padding: '0',
  margin: '0',
  fontWeight: '400',
  fontSize: '16px',
  letterSpacing: '2px',
  textTransform: 'uppercase',
  marginBottom: '22px',
}));

export const StyledMainText = styled('h3')(() => ({
  padding: '0',
  margin: '0',
  marginBottom: '22px',
  fontSize: '38px',
  fontWeight: '700',
  lineHeight: '140%',
}));

export const StyledExtra = styled('p')(() => ({
  padding: '0',
  margin: '0',
  fontSize: '24px',
  lineHeight: '120%',
  maxWidth: '50%',
  marginBottom: '100px',
}))