import {
  StyedAbout,
  StyledContent,
  StyledButton,
  StyledImage,
  StyledIronman,
  StyledDot,
  StyledDots,
  StyledLink,
  StyledSlogan,
  StyledTitle,
  StyledSocial,
  StyledSocialBlock,
  StyledSocialIcon
} from './styled';

import facebook from '../../../../images/facebook_link.png'
import inst from '../../../../images/insta_link.png'
import linkedin from '../../../../images/linkedin_link.png'
import telegram from '../../../../images/telegram_link.png'

import image from '../../../../images/ironman.png';

interface Props {
  pocket: any
};

export const Ironman: React.FC<Props> = ({ pocket }) => {

  const socials = [
    {
      id: 1,
      link: '#',
      icon: facebook
    },
    {
      id: 2,
      link: '#',
      icon: inst
    },
    {
      id: 3,
      link: '#',
      icon: linkedin
    },
    {
      id: 4,
      link: '#',
      icon: telegram
    },
  ]

  return (
    <StyledIronman>
      <StyledImage src={image} />

      <StyledDots>
        <StyledDot />
        <StyledDot />
      </StyledDots>

      <StyledSocialBlock>
        {
          [...socials].map((link) => (
            <StyledSocial key={link.id} href={link.link}>
              <StyledSocialIcon src={link.icon} />
            </StyledSocial>
          ))
        }
      </StyledSocialBlock>

      <StyledContent>
        <StyledLink href='#' >www.Mapleleaf.com</StyledLink>
        <StyledSlogan>get an academic scholarship</StyledSlogan>
        <StyledTitle>{pocket.title}</StyledTitle>
        <StyedAbout>{pocket.about}</StyedAbout>
        <StyledButton>Shop Now</StyledButton>
      </StyledContent>
    </StyledIronman>
  )
}
