import { Button } from '@mui/material';
import { styled } from '@mui/material/styles';

interface GridProps {
  grid: string
}

export const StyledPack = styled('div')<GridProps>(({ grid }) => ({
  display: 'flex',
  gridColumn: grid,
  transition: '0.3s',
  ['&:hover']: {
    transform: 'scale(1.05)'
  },
  /* ['@media(max-width: 1200px)']: {
    gridColumn: '1/3'
  } */
}));

export const StyledContent = styled('div')(() => ({
  filter: 'drop-shadow(0px 147px 59px rgba(0, 0, 0, 0.01)) drop-shadow(0px 83px 50px rgba(0, 0, 0, 0.03)) drop-shadow(0px 37px 37px rgba(0, 0, 0, 0.04)) drop-shadow(0px 9px 20px rgba(0, 0, 0, 0.05)) drop-shadow(0px 0px 0px rgba(0, 0, 0, 0.05))',
  background: '#FCFCFB',
  borderRadius: '8px',
  display: 'flex',
  flexDirection: 'column',
  padding: '10%',
  width: '80%',
  boxSizing: 'border-box',
  /* alignItems: 'center' */
}));

interface ColorProps {
  color: string
}

export const StyledTitle = styled('div')<ColorProps>(({ color }) => ({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  gap: '8px',
  color: color,
  fontSize: '38px',
  fontWeight: '800',
  marginBottom: '40px',
  ['@media(max-width: 1440px)']: {
    fontSize: '24px',
  }
}));

export const StyledIcon = styled('img')(() => ({
  width: '20px'
}));

export const StyledBenefits = styled('div')(() => ({
  display: 'flex',
  flexDirection: 'column',
  gap: '26px',
  padding: '0 12%',
  marginBottom: '40px'
}));

export const StyledBenefit = styled('div')(() => ({
  display: 'flex',
  gap: '24px',
  fontSize: '18px'
}));

interface BackProps {
  back: string
}

export const StyledButton = styled(Button)<BackProps>(({ back }) => ({
  width: '80%',
  padding: '20px 20px',
  color: '#fff',
  fontSize: '16px',
  cursor: 'pointer',
  margin: 'auto',
  borderRadius: '12px',
  background: back,
  minWidth: '0'
}));

export const StyledPrice = styled('div')<BackProps>(({ back }) => ({
  width: '20%',
  height: '70%',
  background: back,
  margin: 'auto',
  borderTopLeftRadius: '8px',
  borderBottomLeftRadius: '8px',
  color: '#fff',
  fontWeight: '800',
  fontSize: '38px',
  filter: 'drop-shadow(0px 147px 59px rgba(0, 0, 0, 0.01)) drop-shadow(0px 83px 50px rgba(0, 0, 0, 0.03)) drop-shadow(0px 37px 37px rgba(0, 0, 0, 0.04)) drop-shadow(0px 9px 20px rgba(0, 0, 0, 0.05)) drop-shadow(0px 0px 0px rgba(0, 0, 0, 0.05))',
  ['@media(max-width: 1440px)']: {
    fontSize: '24px',
  }
}));

export const StyledPriceText = styled('div')(() => ({
  margin: 'auto',
  transform: 'rotate(-90deg) translateX(-270%)'
}))