import { styled } from '@mui/material/styles';

export const StyledContainer = styled('div')(() => ({
  overflow: 'hidden',
  margin: 'auto',
}));

export const StyledGrid = styled('div')(() => ({
  display: 'grid',
  gridTemplateColumns: 'repeat(12, 1fr)',
  gap: '30px',
  margin: 'auto',
  maxWidth: '1440px',
  padding: '0 20px',
  position: 'relative',
  /* maxWidth: '1170px', */
}));