import {
  StyledComments,
  StyledCommentsColumn,
  StyledCommentsContainer,
  StyledPot
} from './styled';

import { Comment } from './Comment/Comment';

import blue from '../../../images/blue.png';

interface Props {
  comments: CommentOne[],
  users: User[]
}

export const Comments: React.FC<Props> = ({ comments, users }) => {

  return (
    <StyledComments>
      <StyledPot src={blue} />
      <StyledPot src={blue} sx={{
        top: '0',
        right: '0',
        transform: 'rotate(180deg)',
        width: '20%',
      }}/>
      <StyledCommentsContainer>
        <StyledCommentsColumn
          sx={{
            gridColumn: '1/2',
            transform: 'translateY(5%)',
            ['@media (max-width: 980px)']: {
              gridColumn: '1/5',
              transform: 'translateX(1%)',
            }
          }}
        >
          {[...comments.slice(0,4).map((comment) =>
            {
              const visibleUser = users.find((user) => user.id === comment.userId);
              return visibleUser &&
                <Comment
                  comment={comment}
                  user={visibleUser}
                  key={comment.id}
                />
            }
          )]}
        </StyledCommentsColumn>
        <StyledCommentsColumn
          sx={{
            gridColumn: '2/3',
            transform: 'translateY(-5%)',
            ['@media (max-width: 980px)']: {
              gridColumn: '1/5',
              transform: 'translateX(-1%)',
            }
          }}
        >
          {[...comments.slice(4,8).map((comment) =>
            {
              const visibleUser = users.find((user) => user.id === comment.userId);
              return visibleUser &&
                <Comment
                  comment={comment}
                  user={visibleUser}
                  key={comment.id}
                />
            }
          )]}
        </StyledCommentsColumn>
        <StyledCommentsColumn
          sx={{
            gridColumn: '3/4',
            transform: 'translateY(10%)',
            ['@media (max-width: 980px)']: {
              gridColumn: '1/5',
              transform: 'translateX(1%)',
            }
          }}
        >
          {[...comments.slice(8,12).map((comment) =>
            {
              const visibleUser = users.find((user) => user.id === comment.userId);
              return visibleUser &&
                <Comment
                  comment={comment}
                  user={visibleUser}
                  key={comment.id}
                />
            }
          )]}
        </StyledCommentsColumn>
        <StyledCommentsColumn
          sx={{
            gridColumn: '4/5',
            transform: 'translateY(0%)',
            ['@media (max-width: 980px)']: {
              gridColumn: '1/5',
              transform: 'translateX(-1%)',
            }
          }}
        >
          {[...comments.slice(12,16).map((comment) =>
            {
              const visibleUser = users.find((user) => user.id === comment.userId);
              return visibleUser &&
                <Comment
                  comment={comment}
                  user={visibleUser}
                  key={comment.id}
                />
            }
          )]}
        </StyledCommentsColumn>
      </StyledCommentsContainer>
    </StyledComments>
  )
}