import { Pack } from './Pack/Pack';
import {
  StyledList
} from './styled';

interface Props {
  pockets: any,
}

export const List: React.FC<Props> = ({ pockets }) => {
  return (
    <StyledList>
      {[...pockets].map((pack) => (
        <Pack
          key={pack.id}
          pack={pack}
        />
      ))}
    </StyledList>
  )
}