import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';

export const StyledFeedback = styled('div')(() => ({
  width: '70%',
  margin: 'auto',
  display: 'flex',
  gap: '80px',
  borderRadius: '16px',
  overflow: 'hidden',
  background: '#fff',
  padding: '0 40px',
}));

export const StyledImage = styled('img')(() => ({
  flex: '0.35',
}))

export const StyledBlock = styled('div')(() => ({
  display: 'flex',
  gap: '20px',
  flexDirection: 'column',
  justifyContent: 'space-between',
  padding: '40px 0',
  flex: '0.65'
}));

export const StyledText = styled('p')(() => ({
  padding: '0',
  margin: '0',
  fontSize: '18px',
  lineHeight: '140%',
  /* marginBottom: '20px', */
}));

export const StyledInfo = styled('div')(() => ({
  display: 'flex',
}));

export const StyledNameAndPosition = styled('div')(() => ({
  display: 'flex',
  flexDirection: 'column',
  gap: '10px',
  marginRight: '60px'
}));

export const StyledLogo = styled('img')(() => ({
  width: '200px',
  height: '60%',
}))

export const StyledName = styled('div')(() => ({
  fontWeight: '600',
  fontSize: '24px'
}));

export const StyledExtra = styled('div')(() => ({
  fontSize: '18px'
}));

export const StyledButtons = styled('div')(() => ({
  display: 'flex',
  gap: '20px',
}));

export const StyledButton = styled(Button)(() => ({
  width: '70px',
  height: '70px',
  borderRadius: '50%',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  color: '#BCBCBC',
  textAlign: 'center',
  boxShadow: '0px 147px 59px rgba(0, 0, 0, 0.01), 0px 83px 50px rgba(0, 0, 0, 0.03), 0px 37px 37px rgba(0, 0, 0, 0.04), 0px 9px 20px rgba(0, 0, 0, 0.05), 0px 0px 0px rgba(0, 0, 0, 0.05)',
  ['&:hover']: {
    color: '#fff',
    background: '#0083FF'
  },
  ['&:focus']: {
    color: '#fff',
    background: '#0083FF'
  }
}))