import { styled } from '@mui/material';

export const StyledFrontPage = styled('section')(() => ({
  height: '100vh',
  background: '#FCFCFB',
  /* marginTop: '110px', */
  marginBottom: '70px',
  zIndex: '1',
  /* padding: '0 20px' */
}));

export const StyledFrontPageContainer = styled('section')(() => ({
  margin: 'auto',
  height: '100%',
  maxWidth: '1440px',
  padding: '0 20px',
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'space-between',
}));