import {
  StyledBottom,
  StyledCopyRight,
  StyledFooter,
  StyledImage,
  StyledLink,
  StyledLinks,
  StyledLinksBlock,
  StyledLinksTitle,
  StyledLogo,
  StyledPrivacy,
  StyledSocial,
  StyledSocialBlock,
  StyledSocialIcon,
} from './styled';

import facebook from '../../../images/facebook_link.png'
import inst from '../../../images/insta_link.png'
import linkedin from '../../../images/linkedin_link.png'
import telegram from '../../../images/telegram_link.png'

import logo from '../../../images/Logo.png'

export const Footer = () => {
  const socials = [
    {
      id: 1,
      link: '#',
      icon: facebook
    },
    {
      id: 2,
      link: '#',
      icon: inst
    },
    {
      id: 3,
      link: '#',
      icon: linkedin
    },
    {
      id: 4,
      link: '#',
      icon: telegram
    },
  ]
  return (
    <StyledFooter>
      <StyledLogo href='#'>
        <StyledImage
          src={logo}
          alt='MapleLeaf Logo'
        />
      </StyledLogo>
      <StyledLinksBlock sx={{gridColumn: '4/7'}}>
        <StyledLinksTitle>Support</StyledLinksTitle>
        <StyledLinks>
          <StyledLink href='#'>Home</StyledLink>
          <StyledLink href='#'>Advantages</StyledLink>
          <StyledLink href='#'>Courses</StyledLink>
          <StyledLink href='#'>About us</StyledLink>
          <StyledLink href='#'>Feedback</StyledLink>
          <StyledLink href='#'>Contacts</StyledLink>
        </StyledLinks>
      </StyledLinksBlock>

      <StyledLinksBlock sx={{gridColumn: '7/10'}}>
        <StyledLinksTitle>Courses</StyledLinksTitle>
        <StyledLinks>
          <StyledLink href='#'>Home</StyledLink>
          <StyledLink href='#'>Advantages</StyledLink>
          <StyledLink href='#'>Courses</StyledLink>
          <StyledLink href='#'>About us</StyledLink>
        </StyledLinks>
      </StyledLinksBlock>

      <StyledLinksBlock sx={{gridColumn: '10/13'}}>
        <StyledLinksTitle>Contacts</StyledLinksTitle>
        <StyledLinks>
          <StyledLink href='#'>Ihor Kalinka</StyledLink>
          <StyledLink href='#'>Advantages</StyledLink>
          <StyledLink href='#'>Courses</StyledLink>
          <StyledLink href='#'>About us</StyledLink>
        </StyledLinks>
        <StyledSocialBlock>
          {
            [...socials].map((link) => (
              <StyledSocial key={link.id} href={link.link}>
                <StyledSocialIcon src={link.icon} />
              </StyledSocial>
            ))
          }
        </StyledSocialBlock>
      </StyledLinksBlock>

      <StyledBottom>
        <StyledCopyRight>
          2023  Maple Leaf tech Academy Inc. Copyright and All rights reserved
        </StyledCopyRight>
        <StyledPrivacy>Terms and Conditions Privacy Policy</StyledPrivacy>
      </StyledBottom>
    </StyledFooter>
  )
}