import { 
  StyledButton,
  StyledContent,
  StyledExtra,
  StyledForm,
  StyledInput,
  StyledJoin,
  StyledPhoto,
  StyledTextBlock,
  StyledTitle
} from './styled';

import photo from '../../../images/join.png'

export const Join = () => {
  return (
    <StyledJoin>
      <StyledContent>
        <StyledTextBlock>
          <StyledTitle>Join our community</StyledTitle>
          <StyledExtra>Make only one click to get one solutions</StyledExtra>
          <StyledForm>
            <StyledInput type='text' placeholder='Enter your email'/>
            <StyledButton>Get</StyledButton>
          </StyledForm>
        </StyledTextBlock>
      </StyledContent>
      <StyledPhoto src={photo} />
    </StyledJoin>
  )
}